.frame-holder {
  margin: 25px 0 5px 5px;
}

.frame-holder > * {
  margin: 0 20px;
}

li {
  list-style-type: none;
}

.frame-header {
  margin: 0 10px 5px;
  font-size: 2em;
  font-family: sans-serif;
}

h2.frame-header {
  margin: 1em 0 0 1em;
  font-size: 1.5em;
}

span.error {
  margin: 0;
  color: red;
  font-size: .8em;
}
